import http from "../http-common";

class OwnerDataService {
    getAll() {
        return http.get("/owner");
    }

    get(id) {
        return http.get(`/owner/${id}`);
    }

    findByName(name) {
        return http.get(`/owner?name=${name}`);
    }

    create(data) {
        console.log("in create with post data")
        return http.post("/owner/", data);
    }

    deleteAll() {
        return http.delete('/owner')
    }
    
    update(id, data) {
        return http.put(`/owner/${id}/`, data);
    }

    delete(id) {
        return http.delete(`/owner/${id}`);
    }
}

export default new OwnerDataService();